import axios from "axios"

export default {
    name: 'GouacheDetail',

    props: {
        gouacheId: {}
    },

    data() {
        return {
            gouache: {}
        }
    },

    methods: {
        getGouache() {
            axios.get('/static/gouaches.json')
                .then(result => {
                    this.gouache = result.data.find(x => x.id == this.gouacheId);
                })
                .catch((error) => {
                    throw error.response.data;
                });
        },

        addToCart() {
            this.$toast.success(`La peinture a été ajoutée à votre panier !`);
        }
    },

    created() {
        this.getGouache();
    }
}