<template>
  <div class="page page-detail" v-if="gouache.id">
    <div class="d-flex">
      <div>
        <h1 class="titre-fin espace">"{{ gouache.name }}"</h1>
        <img :src="require(`@/assets/images/${gouache.data}`)" />
      </div>
      <div class="ms-5">
        <div class="card" style="height: min-content">
          <div class="card-header">Détails</div>
          <div class="card-body">
            <div>
              Nom : <b>{{ gouache.name }}</b>
            </div>
            <div>
              Dimensions :
              <b>{{ gouache.size[0] }}cm x {{ gouache.size[1] }}cm</b>
            </div>
            <div>
              Format : <b>{{ gouache.format }}</b>
            </div>
            <div>
              Prix : <b>{{ gouache.price }}€</b>
            </div>
            <div class="mt-3">
              Tags : <span v-for="(t, index) in gouache.tag" :key="index"><b>{{ t }}</b><span v-if="index != gouache.tag.length - 1">, </span></span>
            </div>
          </div>
        </div>
        <div class="mt-5">
          <button class="btn btn-primary" @click="addToCart()">
            <span class="fa fa-cart-plus me-2"></span>Ajoutez au panier
          </button>
        </div>
        <div class="mt-5">
          <button class="btn btn-secondary" @click="$router.push('/gouaches')">
            <span class="fa fa-palette me-2"></span>Voir les autres gouaches
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script src="./gouache.detail.js">
</script>
